.items-products {
  display: flex;
  margin: 0 -15px;
  flex-wrap: wrap;
}

.items-products__column {
  flex: 0 1 33.333%;
  padding: 0 15px;
  margin: 0px 0px 30px 0px;
}

.item-product {
  padding: 15px;
  position: relative;
  border: 1px solid #f5f5f5;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.item-product__labels {
  position: absolute;
  left: 15px;
  top: 15px;
}

.item-product__label {
  height: 21px;
  margin: 0px 0px 5px 0px;
  padding: 0px 15px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  background-color: #fd8424;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}

.item-product__image {
  height: 260px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
}

.item-product__image img {
  max-width: 100%;
  max-height: 240px;
}

.item-product__body {
  padding: 0px 10px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}

.item-product__title {
  color: #36949f;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  display: inline-block;
  margin: 0px 0px 5px 0px;
  flex: 1 1 auto;
  transition: all .3s ease;
}

.item-product__title span {
  font-weight: 900;
  display: block;
}

.item-product__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  transition: all .3s ease;
}

.item-product__old-price {
  flex: 1 1 100%;
  color: #999999;
  font-size: 18px;
  font-weight: 300;
  text-decoration: line-through;
  text-align: right;
}

.item-product__old-price:after {
  font-size: 14px;
}

.item-product__add {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #e6e6e6 url("../../dist/img/icons/header/cart.png") center no-repeat;
}

.item-product__price {
  color: #333333;
  font-size: 24px;
  font-weight: 900;
}

.item-product__price:after {
  font-size: 18px;
}

.item-product__hover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: transform .3s ease;
  transform: translate(-110%, 0px);
}

.hover-item-product {
  border: 5px solid #f68038;
  background-color: rgba(0, 0, 0, 0.84);
  padding: 15px;
  display: flex;
  flex-direction: column;
  color: #fff;
}

.hover-item-product__title {
  font-size: 16px;
  color: #c2c2c2;
  font-weight: 300;
  margin: 0px 0px 25px 0px;
}

.hover-item-product__title span {
  display: block;
  font-weight: 900;
  color: #ffffff;
  margin: 0px 0px 5px 0px;
}

.hover-item-product__cart-wrapper {
  padding: 10px 0px 0px 0px;
  flex: 1 1 auto;
}

.hover-item-product__cart {
  border-radius: 50%;
  background: #fd8424 url("../../dist/img/icons/header/cart.png") center no-repeat;
  width: 57px;
  height: 57px;
  margin: 0 auto 25px auto;
  display: block;
  transition: all 0.3s ease;
}

.hover-item-product__cart_catalog {
  margin: 0;
}

.hover-item-product__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.hover-item-product__old-price {
  flex: 1 1 100%;
  text-align: right;
  color: #999;
  font-size: 18px;
  font-weight: 300;
  margin: 0px 0px 10px 0px;
  text-decoration: line-through;
}

.hover-item-product__old-price:after {
  font-size: 14px;
}

.hover-item-product__compare {
  background: url("../../dist/img/icons/compare.png") left no-repeat;
  padding: 0px 0px 0px 33px;
  display: inline-block;
  line-height: 21px;
  color: #fff;
}

.hover-item-product__compare span {
  border-bottom: 1px dashed;
  font-weight: 300;
}

.hover-item-product__stock {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  color: #f68038;
  font-weight: 400;
  height: 21px;
  background-color: #fff;
  border-radius: 15px;
  line-height: 16px;
}

.hover-item-product__price {
  font-size: 24px;
  font-weight: 900;
}

.hover-item-product__price:after {
  font-size: 18px;
}

.options-item-product__item {
  margin: 0px 0px 10px 0px;
}

.options-item-product__label {
  margin: 0px 0px 3px 0px;
}

.options-item-product__value {
  color: #f68038;
  font-weight: 900;
}

.arrow-icon {
  background: url("../../dist/img/icons/sl-arrow.svg") 0 0 no-repeat;
  width: 33px;
  height: 30px;
