}

.breadcrumbs__list {
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.breadcrumbs__list li {
  padding: 0px 20px 0px 0px;
  margin: 0px 10px 0px 0px;
  background: url("../../dist/img/icons/li-arrow.png") right/10px no-repeat;
}

.breadcrumbs__list li:last-child {
