.checkbox {
  position: relative;
}

.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}

.checkbox__input:checked + .checkbox__text:before {
  border: 2px solid #009aaf;
}

.checkbox__input:checked + .checkbox__text:after {
  content: '';
  width: 9px;
  height: 9px;
  background-color: #53b5de;
  position: absolute;
  top: 3px;
  left: 3px;
}

.checkbox__text {
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 12px;
  color: #666666;
  line-height: calc(16 / 12);
  cursor: pointer;
}

.checkbox__text:before {
  content: "";
  align-self: flex-start;
  margin: 0px 14px 0px 0px;
  flex: 0 0 15px;
  left: 0;
  top: 0;
  width: 15px;
  height: 15px;
  background: #fff;
  border: 2px solid #dadada;
}

.checkbox a {
  color: #fff;
  text-decoration: underline;
}

.checkbox.error .checkbox__text:before {
  border: 1px solid #ee1212;
}

.option {
