  font-family: "UniSans";
  line-height: 133.33333%;
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: 0.75px;
}

.page-menu__burger {
  flex: 0 0 36px;
  width: 36px;
  height: 36px;
  background-color: #068292;
  display: block;
  position: relative;
  cursor: pointer;
  z-index: 5;
  padding: 10px 6px;
}

.page-menu__burger span {
  transition: all 0.3s ease 0s;
  top: calc(50% - 1px);
  left: 0px;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
}

.page-menu__burger span:first-child {
  top: 0px;
}

.page-menu__burger span:last-child {
  top: auto;
  bottom: 0px;
}

.page-menu__burger.active span {
  transform: scale(0);
}

.page-menu__burger.active span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}

.page-menu__burger.active span:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
}

.page-menu__lines {
  position: relative;
  height: 16px;
}

.page-menu__body {
  display: none;
}

.page-menu__list > li {
