  content: '';
  background: url("../../dist/img/icons/content/arrow-menu.png") 0 0 no-repeat;
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translate(0, -50%);
  height: 14px;
  width: 14px;
}

.page-menu__parent.active .submenu-page__item {
  display: block;
}

