    z-index: 2;
    transition: all .5s ease 0s;
    visibility: hidden;
  }

  .menu__body:before {
    content: '';
    position: fixed;
    width: 100%;
    height: 90px;
    top: 0;
    left: 0;
    background-color: #009cb0;
    z-index: 3;
  }

  .menu__body.active {
